class App extends PureComponent {
  state = {
    selectedNavItem: 'all',
    filters: {
      area: null,
      price: null,
      date: null,
      requiresRegistration: null,
    },
    shows: [],
  };

  async componentDidMount() {
    this.setState({ loading: true });
    const shows = await API.getShows();
    console.log(shows);
    this.setState({
      loading: false,
      shows,
    });
  }

  selectNavItem = selectedNavItem => {
    this.setState({ selectedNavItem });
  };

  handleFilterChange = (value, name) => {
    this.setState(state => ({
      filters: {
        ...state.filters,
        [name]: value,
      },
    }));
  };

  render() {
    const { selectedNavItem, /* filters, */ shows, loading } = this.state;
    return (
      <ThemeProvider theme={theme}>
        <Container>
          <Header>
            <Logo />
            <Nav selected={selectedNavItem} onSelect={this.selectNavItem}>
              <NavItem title="" name="all" />
              <NavItem title="" name="shows" />
              <NavItem title="" name="lectures" />
            </Nav>
            {/* <Filters>
              <FilterItem
                type="select"
                label=""
                name="area"
                value={filters.area}
                onChange={this.handleFilterChange}
                options={AREA_OPTIONS}
              />
              <FilterItem
                type="select"
                label=""
                name="price"
                value={filters.price}
                onChange={this.handleFilterChange}
                options={PRICE_OPTIONS}
              />
              <FilterItem
                type="date"
                label=""
                name="date"
                value={filters.date}
                onChange={this.handleFilterChange}
                minDate={TODAY}
                maxDate={null}
              />
              <FilterItem
                type="select"
                label=" "
                name="requiresRegistration"
                value={filters.requiresRegistration}
                onChange={this.handleFilterChange}
                options={REQUIRED_REGISTRATION_OPTIONS}
              />
            </Filters> */}
          </Header>
          <Progress loading={loading}>
            <Indeterminate />
          </Progress>
          <Shows>{shows.filter(matches(FILTERS[selectedNavItem])).map(show => <Show key={show.id} {...show} />)}</Shows>
        </Container>
      </ThemeProvider>
    );
  }
}

export default App;



